body {
  font-family: 'Montserrat', sans-serif;
  background-color: #1b66d8;
  color: #fff;
}

body::-webkit-scrollbar {
  width: 1em;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px rgb(160, 160, 160);
}

body::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #51e5c4;
  outline: 1px solid black;
}
a {
  color: #0066ff;
  text-decoration: none;
  size: 16px;
  cursor: pointer;
}

a:hover {
  color: #51e5c4;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

.option-icon {
  margin-right: 0.25em;
}
